<template>
  <div class="header">
    <div class="content">
      <div class="box">
        <div class="name">{{ name }}</div>
        <div class="iconfont bottomArrowIcon"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    name() {
      const state = this.$store.state;
      let { type, targetId } = state.chatting.curChattingTarget;
      console.log("targetIddddddd");
      console.log(targetId);
      // group_id
      let target;
      if (type === "user") {
        target = state.user.info.friends.find((item) => item.id === targetId);
      } else if (type === "group") {
        target = state.user.info.groups.find(
          (item) => item.group_id === targetId
        );
      }
      if(!target){
        return "";
      }
      return target.name;
    },
  },
  components: {},
};
</script>

<style lang="scss" scoped>
.header {
  width: 100%;
  height: 51px;
  .content {
    width: 95%;
    height: 100%;
    border-bottom: 1px solid #ccc;
    box-sizing: border-box;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    .box {
      display: flex;
      cursor: pointer;
      .name {
        color: #000;
        margin-right: 13px;
      }
      .bottomArrowIcon {
        color: rgb(167, 166, 166);
      }
    }
  }
}
</style>