  //     display: block;
  //   }
  .myWrapper {
    // float: left;
    flex-grow: 1;
    display: flex;
    align-items: center;
    .avatar {
      width: 40px;
      height: 40px;
      border-radius: 2px;
      margin-right: 10.625px;
    }
    .name {
      color: #fff;
      font-size: 18px;
      text-overflow: ellipsis;
    }
  }
  .config {
